import React, { useRef, useEffect } from "react";
import { ReactComponent as Scene } from './scene.svg';
import gsap from "gsap/all";
import "./App.scss";

const App = () => {
  
  const wrapper = useRef(null);

  useEffect(() => {
    const [elements] = wrapper.current.children;  

    const virus = elements.getElementById("virus");
    const puntos = elements.getElementById("puntos");
    const lineas = elements.getElementById("lineas");

    gsap.set([virus, puntos, lineas], {autoAlpha: 1});

    



  });

   return (
     
       <div className="primeravirus anim" ref={wrapper}>
      
     <Scene />
   </div>
  
   );
}

export default App;